body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

#root {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.show-users {
  margin: 20px 0;
  padding: 10px;
  font-size: 18px;
  border: 1px solid #999999;
  border-radius: 10px;
  background-color: #ffffff;
  cursor: pointer;
  -webkit-transition-duration: 0.15s;
          transition-duration: 0.15s;
}

.show-users:hover {
  background-color: #e6edfd;
  box-shadow: 0 1px 1px 0 #000000;
}
.overall-info {
  margin: 20px 0;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
}
.user-line {
  position: relative;
  margin-bottom: 7px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 0;
  font-weight: 500;
  padding: 10px 15px;
  background-color: #e6edfd;
  border-radius: 5px;
  box-shadow: 0 1px 2px 0 #000000;
  cursor: pointer;
}

.user-line::before {
  content: "";
  position: absolute;
  left: 15px;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  width: 15px;
  height: 15px;
  border: 1px solid #d5d7dd;
  border-radius: 3px;
  background-color: #fefdfe;
}

.user-line::after {
  content: "";
  position: absolute;
  left: 18px;
  top: calc(50% - 1px);
  width: 9px;
  height: 5px;
  border-left: 2px solid #fefdfe;
  border-bottom: 2px solid #fefdfe;
  -webkit-transform: translate(0, -50%) rotate(-45deg);
          transform: translate(0, -50%) rotate(-45deg);
  opacity: 0;
}

.user-line--checked::before {
  background-color: #438ee7;
  border-color: #438ee7;
}

.user-line--checked::after {
  opacity: 1;
}

.user-line__checkbox {
  visibility: hidden;
}

.user-line > *:not(*:first-child) {
  display: inline-block;
  padding-left: 15px;
  font-size: 14px;
}

.user-line__id {
  width: 40px;
}

.user-line__created-at {
  width: 90px;
}

.user-line__name {
  width: 90px;
}

.user-line__email {
  width: 200px;
}

.user-line__description {
  width: 150px;
}
